import React, { useEffect } from 'react'
import { Link, Navigate, useParams } from 'react-router-dom'
import { myOrders, resetOrder, selectCurrentOrder } from '../features/orders/orderSlice'
import { resetCartAsync } from '../features/cart/cartSlice'
import {useDispatch,useSelector} from "react-redux"
import { selectUserInfo } from '../features/user/userSlice'

const OrderSuccess = () => {
  const order = useParams()
  const user = useSelector(selectUserInfo)
  const currentOrder= useSelector(selectCurrentOrder)
  const cartItems = useSelector(state => state.cart.cartItems)
  const dispatch = useDispatch()
  console.log('success',currentOrder)

  useEffect(() => {
    if(cartItems && cartItems.length > 0){

      // clear cart
      dispatch(resetCartAsync())
      // clear current order
      dispatch(resetOrder())
    }

  }, [dispatch,user])

  return (
    <>
      {!order.id && <Navigate to='/' replace={true} />}
      <main className="grid min-h-full place-items-center bg-white px-6 py-24 sm:py-32 lg:px-8">
        <div className="text-center">
          <p className="text-base font-semibold text-indigo-600">Order placed successfully</p>
          <h1 className="mt-4 text-3xl font-bold tracking-tight text-gray-900 sm:text-5xl">Order Id is {order?.id}</h1>
          <p className="mt-6 text-base leading-7 text-gray-600">Sorry, we couldn’t find the page you’re looking for.</p>
          <div className="mt-10 flex items-center justify-center gap-x-6">
            <Link
              to="/"
              className="rounded-md bg-indigo-600 px-3.5 py-2.5 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
            >
              Go back home
            </Link>
            <a href="#" className="text-sm font-semibold text-gray-900">
              Contact support <span aria-hidden="true">&rarr;</span>
            </a>
          </div>
        </div>
      </main>

    </>
  )
}

export default OrderSuccess
